//const wx = require('weixin-js-sdk')
import store from '../store.js'
import qs from 'qs'
import axios from 'axios'
import { MessageBox,Toast,Indicator,TransferDomDirective as TransferDom} from 'mint-ui'

export default {
  directives: {
    TransferDom
  },
  name: 'app',
  data () {
    return {
      status:sessionStorage.status,
      txt:'',
      show2:false,
      name:localStorage.nickname,
      icon:localStorage.icon,
      deatilData:''
    }
  },
  methods: {
    xLog2(){
      this.show2=!this.show2
    },
    xLog1(){
      sessionStorage.toZan=1;
      this.$router.push({
        name:'rank'
      })
    },
    detailFu(){
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/api/getdreambyuser?openid='+localStorage.openid,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
            that.deatilData=res.data.data;
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
    wxShare(e){
      let that = this;
      console.log(wx);
      wx.ready(function(){
        let shareUrl = window.location.protocol+'//'+window.location.host+'/static/ind.html?info='+e+'&sd='+sessionStorage.appId;
        wx.onMenuShareAppMessage({
            title: that.deatilData.title, // 分享标题
            desc: that.deatilData.detail, // 分享描述
            link: shareUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
            imgUrl: that.deatilData.image, // 分享图标
            success: function () {
              // 设置成功
            }
        });
       //  wx.updateAppMessageShareData({
       //     title: that.deatilData.title, // 分享标题
       //     desc: that.deatilData.detail, // 分享描述
       //     link: shareUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
       //     imgUrl: that.deatilData.image, // 分享图标
       //     success: function () {
       //       // 设置成功
       //     }
       // });
       //  wx.updateTimelineShareData({
       //     title: that.deatilData.title, // 分享标题
       //     link: shareUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
       //     imgUrl: that.deatilData.image, // 分享图标
       //     success: function () {
       //       // 设置成功
       //     }
       // });
        // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
      });
      wx.error(function(res){
        // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
        console.log(res);
      });
    }
  },
  created:function(){
    this.detailFu();
    let that =this;
    setTimeout(function(){
      that.wxShare(that.deatilData.dream_id);
    },1000);

    if(this.status==1){
      this.txt ='您已发起梦想'
    }
    if(this.status==2){
      this.txt ='您发起梦想审核中'
    }
    if(this.status==4){
      this.txt ='您还没有发起梦想'
    }
    if(this.status==3){
      this.txt ='您的梦想审核未通过'
    }

  }
}



