import qs from 'qs'
import store from '../store.js'
import axios from 'axios'
import { MessageBox,Toast,Indicator } from 'mint-ui'
import noc from '../assets/nocli.png'
import cli from '../assets/nav3.png'

export default {
  prop:['message'],
  name: 'AppFooter',
  data () {
    return {
      centerImg:'',
      nav2:'/before',
      nav3:true,
      nav5:''
    }
  },
  methods:{
    nav5Fun(){//点击我的
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/api/createcheck?page_type=1&openid='+localStorage.openid,
         //data: qs.stringify(data)
       }).then(function (res) {
         console.log(res.data);
         Indicator.close();
         if(res.data.code==-1){//已发起后台审核过
           //that.detailFu();
           store.commit('detailFun',res.data.data);
           that.$router.push({
             name:'detail'
           });
          sessionStorage.status=1;
        }else if (res.data.code==-2){//审核中
          //that.detailFu();
          store.commit('detailFun',res.data.data);
           that.$router.push({
             name:'detail'
           });
           sessionStorage.status=2;
         }else if (res.data.code==-4){
            //that.detailFu();
            store.commit('detailFun',res.data.data);
            that.$router.push({
              name:'detail'
            });
            sessionStorage.status=4;
         }else if(res.data.code==0){
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }else if(res.data.code==1){
            store.commit('detailFun',res.data.data);
            that.$router.push({
              name:'detail'
            });
            sessionStorage.status=3;
          }else{
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
    detailFu(){
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/api/getdreambyuser?openid='+localStorage.openid,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
           store.commit('detailFun',res.data.data);
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
    nav3Fun(){
      if(this.nav3){
        // Indicator.open('jia');
        let that =this;
        console.log(this.value);
        Indicator.open('加载中');
        axios({
           method: 'get',
           url: '/api/dreamhits?dream_id='+sessionStorage.dream_id+'&openid='+localStorage.openid,
           //data: qs.stringify(data)
         }).then(function (res) {
           Indicator.close();
           if(res.data.code==1){
             that.$router.push({
               name:'result'
             })
             sessionStorage.res=2;
            }else {
              Indicator.close();
              Toast({
                message: res.data.msg,
                duration: 1500
              });
            }
         })
      }else{
        this.$router.push({
          name:'list'
        })
      }
    }
  },
  created:function(){
    if((location.hash.indexOf('info')<0)){
      this.nav3=false
      this.centerImg=noc;
    }else{
      this.centerImg=cli;
    };
  }
}



