
import store from '../store.js'
import qs from 'qs'
import axios from 'axios'
import { MessageBox,Toast,Indicator } from 'mint-ui'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


var cos;
export default {
directives: {
TransferDom
},
name: 'app',
components: {

},
data () {
return {
imgUrl:'',
txt1:'',
show1:false,
show2:false,
popupVisible:false,
pVisible:false,
subFlag:false,
isDone:1,
title:'',
values_index:0,
values:{
values0:"",
values1:""
},

picFunIndex:0,
images:'',
index:-1,
uploadData:{
images0:[],
index0:-1,
},
imgSrc:{
passport_img1:'',

},
}
},
computed:{

preImages(){
return this.uploadData['images'+this.picFunIndex].map(v=>{return v.src});
},
},
methods:{
inputBlur(){
window.scrollTo(0, 0)
},
htmlScroll(){
this.timer = setTimeout(() => {
window.scrollTo(0,0)

},10)
},
handleVisibleChange (isVisible) {
this.pVisible = isVisible;
},
getList(e){
e==0?this.options = this.options0:this.options = this.options1;
this.values_index=e;
this.popupVisible=true;
},
radioFun(){
for(var i=0;i<this.options.arr.length;i++){
if(this.values['values'+this.values_index]==this.options.arr[i].value){
this.label['label'+this.values_index]=this.options.arr[i].label
}
}
this.selType['selType'+(this.values_index+1)]=this.values['values'+this.values_index];
this.popupVisible=false;
},
changeAddress(res){
console.log(res);
},
popFun(){
this.popupVisible=false;
},
formFun(){
this.$vux.confirm.show({
content:'是否确定提交您上传的文件？',
confirmText:'确认提交',

onCancel : () => {
console.log(this) 
},
onConfirm : () => {}
})

},


picFun(e){
this.picFunIndex=e;
this.images=this.uploadData['images'+this.picFunIndex];
},

fileupload(){
let that =this;
Indicator.open('加载中');
console.log(that.images);
let data = new FormData()

data.append("openid",localStorage.openid);
data.append("image",that.images);
this.axios({
method: 'post',
url: '/api/fileupload',
headers: { 'content-type': 'multipart/form-data' },
data: data
}).then(function (res) {
Indicator.close();
if(res.data.code==1){
that.imgUrl=res.data.data.image;
that.s_imgUrl=res.data.data.s_image;
}else {
Indicator.close();
Toast({
message: res.data.msg,
duration: 1500
});
}
})
},

/**
*  绑定函数 -- 选择图片
*/
bindtap_chooseImages(e){
let that = this;

if (Array.isArray(e)) {
let file = e[0].file
console.log(e[0]);
that.images=file;
that.uploadData['images'+that.picFunIndex] = that.uploadData['images'+that.picFunIndex].concat(e);
that.fileupload();



}else {
console.log(res);
}
},
/**
*  绑定函数 -- 删除图片
*/
bindtap_delete(){
this.images = this.images.filter((v,i) => {
return this.index!=i;
})
},
/**
*  绑定函数 -- 预览图片
*/
bingtap_preview(i,j){
this.delFlag = true;
this.picFunIndex=i;
this.uploadData['index'+i] = j;
},
/**
*  绑定函数 -- 取消预览
*/
bingtap_hiddenImg() {
this.delFlag = false;
this.uploadData['index'+this.picFunIndex] = -1;
},
/**
*  绑定函数 -- 上传图片
*/
delFun(){
if(this.picFunIndex<4){
this.imgSrc['passport_img'+(this.picFunIndex+1)]='';
}else{
this.lease = this.lease.splice(1,1);
};
this.delFlag = false;
this.uploadData['index'+this.picFunIndex] = -1;
this.uploadData['images'+this.picFunIndex]=[];

},
lookFun(){

},
xLog1(){

let that =this;
Indicator.open('加载中');
console.log(that.images);
let data = new FormData()
data.append("openid",localStorage.openid);
data.append("title",that.title);
data.append("detail",that.txt1);
data.append("image",that.imgUrl);
data.append("s_image",that.s_imgUrl);

this.axios({
method: 'post',
url: '/api/createdream',
headers: { 'content-type': 'multipart/form-data' },
data: data
}).then(function (res) {
Indicator.close();
if(res.data.code==1){
that.$router.push({
name:"result"
});
sessionStorage.res=1;
}else {
Indicator.close();
Toast({
message: res.data.msg,
duration: 1500
});
}
})
},
xLog2(){
this.show2=!this.show2;
}
},
created:function(){

}
}



