import { MessageBox,Toast,Indicator } from 'mint-ui'
import store from '../store.js'
import qs from 'qs'
import axios from 'axios'

export default {
  name: 'app',
  data () {
    return {
      list:sessionStorage.listCli,
      results:[],
      value: '',
      listArr:[

      ]
    }
  },
  created:function(){
    let that =this;
    Indicator.open('加载中');
    this.axios({
       method: 'get',
       url: '/api/list?page=1',
       //data: qs.stringify(data)
     }).then(function (res) {
       Indicator.close();
       if(res.data.code==1){
         that.listArr = res.data.data;
        }else {
          Indicator.close();
          Toast({
            message: res.data.msg,
            duration: 1500
          });
        }
     })
  },
  methods:{
    getResult (val) {
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/api/search?keyword='+that.value,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
           that.listArr = res.data.data;
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
      // console.log('on-change', val)
      // this.results = val ? getResult(this.value) : []
    },
    toInfo(m,n){
      let that =this;
      Indicator.open('加载中');
      sessionStorage.userImg=n;
      this.axios({
         method: 'get',
         url: '/api/getdreambyid?user_id='+m,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
           store.commit('infoFun2',res.data.data);
           that.$router.push({
             name:'info'
           })
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    }
  },

}
function getResult (val) {
  let rs = []
  for (let i = 0; i < 20; i++) {
    rs.push({
      title: `${val} result: ${i + 1} `,
      other: i
    })
  }
  return rs
}



