import qs from 'qs'
import axios from 'axios'
import store from '../store.js'
import { MessageBox,Toast,Indicator } from 'mint-ui'

export default {
  name: 'app',
  data () {
    return {
      usernum:'',
      username:''
    }
  },
  methods: {

    goHome(){
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'POST',
         url: '/api/userupdate',
         headers: { 'content-type': 'application/x-www-form-urlencoded' },
         data: qs.stringify({name:that.username,phone:that.usernum,openid:localStorage.openid})
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){//无绑定
           that.$router.push({
             name:'home'
           })
         }else {
           Indicator.close();
           Toast({
             message: res.data.msg,
             duration: 1500
           });
          }
       })

    },
    close(){
      history.go(-1)
    },
    inputBlur(){
      window.scrollTo(0, 0)
    }
  },
  created:function(){

  }
}



