import { MessageBox,Toast,Indicator } from 'mint-ui'
import qs from 'qs'
import store from '../store.js'

export default {
  name: 'app',
  data () {
    return {
      searchFlag:true,
      toZan:sessionStorage.toZan,
      has_next_page:'',
      ind:1,
      ind1:1,
      act2:'fr',
      act1:'fl act',
      list:2,
      results:[],
      value: '',
      listArr:[]
    }
  },
  created:function(){
    if(this.toZan==1){
      this.act2 ='fr act';
      this.act1 ='fl';
      this.myZan();
      this.list=1;
    }else{
      this.act1 ='fl act';
      this.act2 ='fr';
      this.page();
      this.list=2;
    }
  },
  mounted(){
    window.addEventListener('scroll',this.handleScroll,true);
  },
  methods:{
    focusFun(){
      this.searchFlag=false;
      this.list=1;
      // this.listArr=[];
    },
    blurFun(){
      this.searchFlag=true;
      this.list=2;
    },
    handleScroll(e){
      let that =this;
        //变量scrollTop是滚动条滚动时，距离顶部的距离
        var scrollTop = e.target.scrollTop;
        //变量windowHeight是可视区的高度
        var windowHeight = e.target.clientHeight;
        //变量scrollHeight是滚动条的总高度
   		var scrollHeight = e.target.scrollHeight;
        //滚动条到底部的条件
        if(scrollTop+windowHeight==scrollHeight){
            //写后台加载数据的函数
            if((location.hash.indexOf('rank')<0)){
              // alert(1);
            }else{
              if(that.has_next_page==1){
                if(that.list==2){
                  that.ind++;
                  that.page();
                }else{
                  that.ind1++;
                  that.myZan();
                }
              }
            };

        }
    },
    myZan(){
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/api/myhitslist?page='+that.ind1+'&openid='+localStorage.openid,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
           if(that.ind1==1){
             that.listArr=res.data.data;
           }else{
             that.listArr =that.listArr.concat(res.data.data);
           }
           that.has_next_page =res.data.has_next_page;
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
    page(){
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/api/list?page='+that.ind+'&openid='+localStorage.openid,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
           if(that.ind==1){
             that.listArr=res.data.data;
           }else{
             that.listArr =that.listArr.concat(res.data.data);
           }
           that.has_next_page =res.data.has_next_page;
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
    getList(e){
      this.ind=1,this.ind1=1;

      if(e==1){
        this.act1 ='fl act';
        this.act2 ='fr';
        this.page();
        this.list=2;
      }else {
        this.act2 ='fr act';
        this.act1 ='fl';
        this.myZan();
        this.list=1;
      }
    },
    getResult (val) {
      if(val==''){
        this.page();
      }else{
        let that =this;
        Indicator.open('加载中');
        this.axios({
           method: 'get',
           url: '/api/search?keyword='+that.value+'&openid='+localStorage.openid+'&page='+that.ind
           //data: qs.stringify(data)
         }).then(function (res) {
           Indicator.close();
           if(res.data.code==1){
             that.list=1;
             that.listArr = res.data.data;
            }else {
              Indicator.close();
              Toast({
                message: res.data.msg,
                duration: 1500
              });
              that.listArr=[];
            }
         })
      }

      // console.log('on-change', val)
      // this.results = val ? getResult(this.value) : []
    },
    toInfo(m,n){
      let that =this;
      console.log(this.value);
      //Indicator.open('加载中');
      sessionStorage.userImg=n;
      sessionStorage.isIndex=1;
      this.axios({
         method: 'get',
         url: '/api/getdreambyid?openid='+localStorage.openid+'&dream_id='+m,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==1){
           store.commit('infoFun2',res.data.data);
           sessionStorage.userImg=n;
           that.$router.push({
             name:'info'
           });
           sessionStorage.dream_id=m;
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    }
  },

}
function getResult (val) {
  let rs = []
  for (let i = 0; i < 20; i++) {
    rs.push({
      title: `${val} result: ${i + 1} `,
      other: i
    })
  }
  return rs
}



