

import Vue from 'vue'
import App from './index.vue'
import './assets/css/reset.css'
import 'lib-flexible/flexible.js'
import VueRouter from "vue-router"
import header from './components/header.vue'
import footer from './components/footer.vue'

import home from './router/home.vue'
import login from './router/login.vue'
import result from './router/result.vue'
import list from './router/list.vue'
import info from './router/info.vue'
import detail from './router/detail.vue'
import load from './router/load.vue'
import content from './router/content.vue'
import rank from './router/rank.vue'
import before from './router/before.vue'






import axios from 'axios'
import VueAxios from 'vue-axios'
import {ImageUpload , ImagePreview} from 'vue-image-upload-preview'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import 'mint-ui/lib/style.css'
import {TabItem, TabContainer, TabContainerItem, Field, Button,Actionsheet,Radio,Checklist,DatetimePicker,Popup} from 'mint-ui'
Vue.config.productionTip = false

Vue.component(header.name, header)
Vue.component(footer.name, footer)

Vue.component('x-dialog', XDialog)
Vue.component('search', Search)
Vue.component('tabbar', Tabbar)
Vue.component('tabbar-item', TabbarItem)
Vue.component('swiper', Swiper)
Vue.component('x-input', XInput)
Vue.component('flow', Flow)
Vue.component('flow-state', FlowState)
Vue.component('flow-line', FlowLine)
Vue.component('group', Group)
Vue.component('popup-picker', PopupPicker)
Vue.component('cell', Cell)
Vue.component('timeline', Timeline)
Vue.component('timelineItem', TimelineItem)

Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)


Vue.component(TabContainer.name, TabContainer)
Vue.component(TabContainerItem.name, TabContainerItem)

Vue.component(TabItem.name, TabItem)
Vue.component(Field.name, Field)
Vue.component(Button.name, Button)
Vue.component(Actionsheet.name, Actionsheet)
Vue.component(Radio.name, Radio)
Vue.component(Checklist.name, Checklist)
Vue.component(DatetimePicker.name, DatetimePicker)
Vue.component(Popup.name, Popup)
Vue.component('image-preview', ImagePreview)
Vue.component('image-upload', ImageUpload)

Vue.use(VueRouter)
Vue.use(VueAxios, axios)

const router = new VueRouter({
routes: [

{ path: '/home', component: home,name:'home' ,meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/load', component: load,name:'load' ,meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/detail', component: detail,name:'detail' ,meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/result', component: result,name:'result' ,meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/list', component: list,name:'list' ,meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/content', component: content,name:'content',meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/info', component: info,name:'info' ,meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/rank', component: rank,name:'rank' ,meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/login', component: login,name:'login',meta: { title: "你分享我点赞大型系列活动" }},
{ path: '/before', component: before,name:'before',meta: { title: "你分享我点赞大型系列活动" }}

]
})
router.beforeEach((to, from, next) => {//beforeEach是router的钩子函数，在进入路由前执行
if (to.meta.title) {//判断是否有标题
document.title = to.meta.title
}
next()
})
const handler = (e) => {
e.preventDefault();
};
Vue.directive('roll', {
componentUpdated(el, binding) {
if (binding.value instanceof Array) {
const visible = binding.value.some(e => e); 
if (visible) {
document.getElementsByTagName('body')[0].addEventListener('touchmove', handler, { passive: false });
} else {
document.getElementsByTagName('body')[0].removeEventListener('touchmove', handler, { passive: false });
}
} else if (typeof binding.value === 'boolean') {
if (binding.value) {
document.getElementsByTagName('body')[0].addEventListener('touchmove', handler, { passive: false });
} else {
document.getElementsByTagName('body')[0].removeEventListener('touchmove', handler, { passive: false });
}
}
}
});


/* eslint-disable no-new */
new Vue({
el: '#app',
router,
components:{
App
},
template: '<App/>'
})



