import store from '../store.js'
import qs from 'qs'
import axios from 'axios'
import { MessageBox,Toast,Indicator } from 'mint-ui'

export default {
  name: 'app',
  data () {
    return {

    }
  },
  methods: {
    nav2Fun(){//点击发起
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/api/createcheck?page_type=2&openid='+localStorage.openid,
         //data: qs.stringify(data)
       }).then(function (res) {
         Indicator.close();
         if(res.data.code==-3){//无绑定
           that.$router.push({
             name:'login'//绑定也
           });
         }else if(res.data.code==1 || res.data.code==-4) {
            that.$router.push({
              name:'home'//发起梦想页
            });
          }else {
            Indicator.close();
            Toast({
              message: res.data.msg,
              duration: 1500
            });
          }
       })
    },
  },
  created:function(){

  }
}



